import React, { Component } from "react";
import { connect } from "react-redux";
import ArrayLine from "../ArrayLine";
import AvarageRow from "../AvarageRow";
import styles from "./Array.module.css";
import * as selectors from "../../redux/selectors";
import getClosestNumbers from "../../services/closestNumbers";

class Array extends Component {
  state = [];

  illuminateOnHandler = e => {
    const { id } = e.target;
    if (id.slice(0, 6) !== "number") {
      return;
    }
    const { array, numberQty } = this.props;
    const closestNumbers = getClosestNumbers(array, id, numberQty);
    this.setState(closestNumbers);
  };

  illuminateOffHandler = e => {
    const { id } = e.target;
    if (id.slice(0, 6) !== "number") {
      return;
    }
    this.setState([]);
  };

  render() {
    const { array } = this.props;

    return (
      <div>
        <table
          className={styles.matrix}
          onMouseOver={this.illuminateOnHandler}
          onMouseOut={this.illuminateOffHandler}
        >
          <tbody>
            {array.map(line => (
              <ArrayLine
                key={line.id}
                id={line.id}
                line={line.cells}
                closestNumbers={this.state}
              />
            ))}
            <AvarageRow />
          </tbody>
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    array: selectors.getArray(state),
    numberQty: selectors.getNumbersQty(state)
  };
};

export default connect(mapStateToProps)(Array);
